.pricing__section {
  padding-left: 15px;
  padding-top: 43px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    background: #F4FAFF;
    padding-bottom: 62px;
  }
  
  .pricing__container-card {
    background:#252525;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.2);
    width: 280px;
    height: 500px;
    text-decoration: none;
    border-radius: 4px;
    display: flex;
    flex-direction: row;
    padding: 10px;
  }
  .pricing__container-card .pricing__container-cardInfo{
    padding-right: 10px;
    flex-grow: 1;
    width: 33%;
    position:relative;
  }
  .pricing__btn{
    position:absolute;
bottom:34px;
margin-left: -25%;

color: #fff;
    background-color: #07516e;
    width: 140px;
    height: 35px;
    border-radius: 28px;
  }
  .pricing__btn:hover{
    color: #FF9C1E;
  }
  

  
  .pricing__container-card:hover {
    transform: scale(1.06);
    transition: all 0.3s ease-out;
    color: #1c2237;
  }
  
  .pricing__container-card:nth-child(2) {
    margin: 0 24px;
  }
  .pricing__container-card:nth-child(3) {
    margin: 0 24px;
  }
  
  .pricing__container {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .pricing__wrapper {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin: 0 auto;
  }
  
  .pricing__heading {
    color: #07516E;
    margin-bottom: 24px;
  }
  .pricing__description{
    color: #07516E;
    margin-bottom: 24px;
  }

  .pricing__container-cardInfo {
    display: flex;
    flex-direction: column;
    height: 106%;
    padding: 24px;
    align-items: center;
    color: #fff;
  }
  
  .pricing__container-cardInfo h3 {
    margin-bottom: 5px;
    font-size: 24px;
  }
  
  .pricing__container-cardInfo h4 {
    font-size: 40px;
  }
  
  .pricing__container-cardInfo p {
    font-size: 14px;
    margin-bottom: 24px;
  }
  
  .pricing__container-features {
   font-size: 9px;
    list-style: none;
  }
  
  .pricing__container-features li {
    margin-bottom: 10px;
  }
  
